.BottomComponentArea {
  background-color: #202024;
  color: white;
  display: flex;
  align-content: center;
  justify-content: center;
  min-height: 150px;
  .ListArea {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 90%;

    .IconBox {
      // 아이콘에 대한 css 입니다.
      display: flex;
      align-items: center;
      justify-content: center;

      a {
        text-decoration: none;
        color: none;
        
      }

      .Icons {
        color: rgb(235, 231, 231, 0.3);
        min-width: 30px;
        min-height: 30px;
        margin: 0 10px;
        transition: 0.3s;
      }

      .Icons:hover {
        color: white;
      }

      .Velog {
        font-weight: 700;
        color: rgb(235, 231, 231, 0.3);
        transition: 0.3s;
      }

      .Velog:hover {
        color: #12b886;
      }
    }
  }
}

